<template>
  <div class="home container">
    <!-- add/updata Modal -->
    <div
      class="modal fade"
      id="exampleModalLong"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLongTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 v-if="isnew === true" class="modal-title text-primary font-weight-bold">新增</h5>
            <h5 v-else class="modal-title text-primary font-weight-bold">修改</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="SKU">SKU</label>
              <input
                type="text"
                class="form-control"
                id="SKU"
                placeholder="SKU"
                v-model="tempData.SKU"
              >
            </div>
            <div class="row">
              <div class="col">
                <div class="form-group">
                  <label for="L">L （cm）</label>
                  <input
                    type="number"
                    class="form-control"
                    id="L"
                    placeholder="L"
                    v-model="tempData.L"
                  >
                </div>
              </div>
              <div class="col">
                <div class="form-group">
                  <label for="W">W（cm）</label>
                  <input
                    type="number"
                    class="form-control"
                    id="W"
                    placeholder="W"
                    v-model="tempData.W"
                  >
                </div>
              </div>
              <div class="col">
                <div class="form-group">
                  <label for="H">H（cm）</label>
                  <input
                    type="number"
                    class="form-control"
                    id="H"
                    placeholder="H"
                    v-model="tempData.H"
                  >
                </div>
              </div>
            </div>
            <div class="form-group">
              <label for="Weighing">材积重</label>
              <input
                type="number"
                class="form-control"
                id="Weighing"
                placeholder="運算中..."
                v-model="sum"
                disabled
              >
            </div>
            <div class="form-group">
              <label for="unit">单位</label>
              <input
                type="text"
                class="form-control"
                id="unit"
                placeholder="unit"
                v-model="tempData.unit"
              >
            </div>
            <div class="form-group">
              <label for="ActualWeight">实际重量</label>
              <input
                type="number"
                class="form-control"
                id="ActualWeight"
                placeholder="实际重量"
                v-model="tempData.ActualWeight"
              >
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-secondary mr-auto" data-dismiss="modal">Close</button>
            <button v-if="isnew === true" type="button" class="btn btn-primary" @click="addData">儲存</button>
            <button v-else type="button" class="btn btn-primary" @click="edit">修改</button>
          </div>
        </div>
      </div>
    </div>
    <!-- add/updata Modal -->

    <!-- deleteModal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger">
            <h5 class="modal-title text-white" id="deleteModalLabel">確定要刪除嗎?</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body text-center">
            <h1>{{ tempData.SKU }}</h1>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary mr-auto" data-dismiss="modal">取消</button>
            <button type="button " class="btn btn-danger" @click="deleteData(tempData.uid)">刪除</button>
          </div>
        </div>
      </div>
    </div>

    <h1 class="text-center my-5">4PX ({{ skuData.length }})</h1>
    <!-- <button @click="addData">addData</button> -->
    <button
      class="btn btn-primary mr-3"
      data-toggle="modal"
      data-target="#exampleModalLong"
      @click="addModal"
    >新增</button>
    <button class="btn btn-primary" @click="export2Excel">Export</button>
    <div class="search">
      <input type="text" v-model="searchProduct" placeholder="Search">
    </div>
    <div class="table-responsive-md">
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col">SKU</th>
            <th scope="col">L （cm）</th>
            <th scope="col">W（cm）</th>
            <th scope="col">H（cm）</th>
            <th scope="col">材积重</th>
            <th scope="col">单位</th>
            <th scope="col">实际重量</th>
            <th scope="col" class="text-center">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in skuData" :key="item.uid">
            <td class="font-weight-bold">{{ item.SKU }}</td>
            <td>{{ item.L }}</td>
            <td>{{ item.W }}</td>
            <td>{{ item.H }}</td>
            <td
              :class="{'text-danger':item.Weighing*1>item.ActualWeight*1,'font-weight-bold':item.Weighing*1>item.ActualWeight*1,'text-primary':item.Weighing*1 ==item.ActualWeight*1,'font-weight-bold':item.Weighing*1 ==item.ActualWeight*1}"
            >{{ item.Weighing * 1 | rounding }}</td>
            <td>{{ item.unit }}</td>
            <td
              :class="{'text-danger':item.ActualWeight*1>item.Weighing*1,'font-weight-bold':item.ActualWeight*1>item.Weighing*1,'text-primary':item.Weighing*1 ==item.ActualWeight*1,'font-weight-bold':item.Weighing*1 ==item.ActualWeight*1}"
            >{{ item.ActualWeight * 1 | rounding }}</td>
            <td>
              <div class="text-center">
                <button class="btn btn-outline-info mr-2" @click="showModal(item)">
                  <i class="fas fa-edit"></i>
                </button>
                <button class="btn btn-outline-danger" @click="showDelete(item)">
                  <i class="fas fa-trash-alt"></i>
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "home",
  data() {
    return {
      isnew: true,
      excel: [],
      excel_data: [],
      tempData: [],
      searchProduct: ""
    };
  },
  filters: {
    rounding(value) {
      return value.toFixed(1);
    }
  },
  computed: {
    skuData() {
      const vm = this;
      return this.excel_data.filter(item => {
        return (
          item.SKU.toLowerCase().indexOf(vm.searchProduct.toLowerCase()) > -1
        );
      });
    },
    sum: {
      get() {
        const vm = this;
        if (
          isNaN(vm.tempData.L) === false &&
          isNaN(vm.tempData.W) === false &&
          isNaN(vm.tempData.H) === false
        ) {
          return (vm.tempData.Weighing =
            (vm.tempData.L / 100) *
            (vm.tempData.W / 100) *
            (vm.tempData.H / 100) *
            250).toFixed(1);
        }
      },
      set(value) {
        console.log("computed setter" + value);
      }
    }
  },
  methods: {
    addModal() {
      this.isnew = true;
      this.tempData = { unit: "kg" };
    },
    addData() {
      const data = {
        SKU: this.tempData.SKU,
        L: this.tempData.L,
        W: this.tempData.W,
        H: this.tempData.H,
        Weighing: this.tempData.Weighing,
        unit: this.tempData.unit,
        ActualWeight: this.tempData.ActualWeight
      };
      this.$http
        .post("https://pxx-2bd05.firebaseio.com/4PX/4PX.json", data)
        .then(() => {
          this.$("#exampleModalLong").modal("hide");
          this.getData();
        });
    },
    showModal(item) {
      this.isnew = false;
      this.$("#exampleModalLong").modal("show");
      this.tempData = Object.assign({}, item);
    },

    edit() {
      this.$http
        .patch(
          `https://pxx-2bd05.firebaseio.com/4PX/4PX/${this.tempData.uid}.json`,
          this.tempData
        )
        .then(() => {
          this.$("#exampleModalLong").modal("hide");
          this.getData();
        });
    },
    showDelete(item) {
      this.$("#deleteModal").modal("show");
      this.tempData = Object.assign({}, item);
    },
    deleteData(uid) {
      this.$http
        .delete(`https://pxx-2bd05.firebaseio.com/4PX/4PX/${uid}.json`)
        .then(() => {
          this.$("#deleteModal").modal("hide");
          this.getData();
        });
    },
    getData() {
      this.excel = [];
      this.excel_data = [];
      this.$http
        .get("https://pxx-2bd05.firebaseio.com/4PX/4PX.json")
        .then(res => {
          this.excel = res.data;
          Object.keys(this.excel).forEach(key => {
            this.excel_data.push({ ...this.excel[key], uid: key });
          });
        });
    },
    //Excel匯出
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export2Excel() {
      let vm = this;
      require.ensure([], () => {
        const { export_json_to_excel } = require("../vendor/Export2Excel");
        const tHeader = [
          "SKU",
          "L （cm）",
          "W（cm）",
          "H（cm）",
          "材积重",
          "单位",
          "实际重量"
        ];
        const filterVal = [
          "SKU",
          "L",
          "W",
          "H",
          "Weighing",
          "unit",
          "ActualWeight"
        ];
        const list = this.excel_data;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "4PX");
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>


